@media only screen and (min-width: 480px) {
  body {
    font-family: Montserrat;
  }
  body header {
    display: flex;
    width: 100%;
  }
  body header .conteneur-header-logo-nav {
    display: flex;
    padding: 20px;
    width: 100%;
  }
  body header .conteneur-header-logo-nav .kasaLogo {
    width: 50%;
    height: 63px;
  }
  body header .conteneur-header-logo-nav .nav-header {
    display: flex;
    gap: 0;
    width: 50%;
    align-items: center;
    justify-content: space-around;
  }
  body header .conteneur-header-logo-nav .nav-header a {
    text-decoration: none;
    color: black;
  }
  body #carrousel #carrouselImg {
    width: 335px;
    height: 255px;
  }
}